'use client';

import { useState, useEffect } from 'react';
import { useRouter } from 'next/navigation';
import { useSession, signIn } from 'next-auth/react';
import PricingPlans from '@/components/PricingPlanCard';
import { useMatchingAnalysis } from '@/components/MatchingAnalysisContext';

const VirtualTryOn = () => {
  const router = useRouter();
  const { data: session, status } = useSession();
    const [userImage, setUserImage] = useState(null);
  const [apparelImage, setApparelImage] = useState(null);
  const [userImagePreview, setUserImagePreview] = useState(null);
  const [apparelImagePreview, setApparelImagePreview] = useState(null);
  const [loading, setLoading] = useState(false);
  const [resultImageUrl, setResultImageUrl] = useState(null);
  const [error, setError] = useState(null);
  const [tryOnCount, setTryOnCount] = useState(0);
  const [showPricingPlans, setShowPricingPlans] = useState(false);

  const userEmail = session?.user?.email;

  useEffect(() => {
    if (!userEmail || status !== 'authenticated') return;
    
    const storedCount = sessionStorage.getItem('tryOnCount');
    if (storedCount) {
      setTryOnCount(parseInt(storedCount));
    }
  }, [userEmail, status]);

  const handleUserImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setUserImage(file);
      setUserImagePreview(URL.createObjectURL(file));
    }
  };

  const handleApparelImageChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      setApparelImage(file);
      setApparelImagePreview(URL.createObjectURL(file));
    }
  };

  const uploadImage = async (file) => {
    const formData = new FormData();
    formData.append('file', file);
    
    try {
      const response = await fetch(process.env.NEXT_PUBLIC_UPLOAD_API_URL, {
        method: 'POST',
        body: formData,
      });
      
if (!response.ok) {
  console.error('Response Status:', response.status);
  console.error('Response Status Text:', response.statusText);
  const errorDetails = await response.text();
  console.error('Error Details:', errorDetails);
  throw new Error(`Network response was not ok: ${response.status} ${response.statusText} - ${errorDetails}`);
}
      
      const data = await response.json();
      return data.url;
    } catch (error) {
      console.error('Error uploading image:', error);
      throw new Error('Failed to upload image');
    }
  };

  const handleMatchingAnalysis = async () => {
    if (!userImage || !apparelImage) {
      setError('Please upload both images first.');
      return;
    }

    if (status !== 'authenticated') {
      alert('You need to log in to analyze fit.');
      signIn();
      return;
    }

    setLoading(true);
    setError(null);

    try {
      const formData = new FormData();
      formData.append('userImage', userImage);
      formData.append('apparelImage', apparelImage);
      formData.append('userEmail', userEmail);

      const response = await fetch(process.env.NEXT_PUBLIC_FITANALYSIS_API_URL, {
        method: 'POST',
        body: formData,
      });

if (!response.ok) {
  console.error('Response Status:', response.status);
  console.error('Response Status Text:', response.statusText);
  const errorDetails = await response.text();
  console.error('Error Details:', errorDetails);
  throw new Error(`Network response was not ok: ${response.status} ${response.statusText} - ${errorDetails}`);
}

      const data = await response.json();
      
      if (data?.analysis) {
        router.push({
          pathname: '/matchingAnalysis',
          query: { analysis: JSON.stringify(data.analysis) }
        });
      } else {
        throw new Error('No analysis in response');
      }
    } catch (err) {
      console.error('Error during matching analysis:', err);
      setError('Failed to analyze fit. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  const handleTryOn = async () => {
    if (!userImage || !apparelImage) {
      setError('Please upload both images first.');
      return;
    }

    if (status !== 'authenticated') {
      alert('You need to log in to generate a try-on result.');
      signIn();
      return;
    }

    if (tryOnCount >= 3) {
      setShowPricingPlans(true);
      return;
    }

    setLoading(true);
    setError(null);

    try {
      const formData = new FormData();
      formData.append('userImage', userImage);
      formData.append('apparelImage', apparelImage);
      formData.append('userEmail', userEmail);

      const response = await fetch(process.env.NEXT_PUBLIC_FYUSEAPI, {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) {
        let errorMessage = 'Network response was not ok';
if (response.status === 404) {
  errorMessage = 'Resource not found';
} else if (response.status === 500) {
  errorMessage = 'Internal server error';
}
throw new Error(`${errorMessage}: ${response.status} ${response.statusText} - ${errorDetails}`);
      }

      const data = await response.json();
      
      if (data?.generatedImageUrl) {
        setResultImageUrl(data.generatedImageUrl);
        const newCount = tryOnCount + 1;
        setTryOnCount(newCount);
        sessionStorage.setItem('tryOnCount', newCount.toString());
      } else {
        throw new Error('No generated image URL in response');
      }
    } catch (err) {
      console.error('Error during try-on:', err);
      setError('Failed to generate try-on. Please try again.');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="flex flex-col items-center justify-center w-full space-y-8">
      {showPricingPlans ? (
        <PricingPlans onClose={() => setShowPricingPlans(false)} />
      ) : (
        <div className="bg-[#1a1a2f] w-full max-w-4xl rounded-lg shadow-md p-8 space-y-6">
          <h2 className="text-2xl font-medium text-white text-center mb-4">
            Step 1: Upload Your Photos
          </h2>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            <div className="border border-gray-200 rounded-lg p-6 text-center">
              <h3 className="text-lg font-medium text-white mb-4">Your Photo</h3>
              <input
                type="file"
                accept="image/*"
                onChange={handleUserImageChange}
                className="hidden"
                id="userPhoto"
              />
              <label htmlFor="userPhoto" className="cursor-pointer">
                {userImagePreview ? (
                  <img
                    src={userImagePreview}
                    alt="User Preview"
                    className="mx-auto max-h-48 object-contain rounded-lg"
                  />
                ) : (
                  <div className="flex flex-col items-center justify-center h-48">
                    <p className="text-white">Click to upload</p>
                  </div>
                )}
              </label>
            </div>
            <div className="border border-gray-200 rounded-lg p-6 text-center">
              <h3 className="text-lg font-medium text-white mb-4">Clothing Item</h3>
              <input
                type="file"
                accept="image/*"
                onChange={handleApparelImageChange}
                className="hidden"
                id="apparelPhoto"
              />
              <label htmlFor="apparelPhoto" className="cursor-pointer">
                {apparelImagePreview ? (
                  <img
                    src={apparelImagePreview}
                    alt="Apparel Preview"
                    className="mx-auto max-h-48 object-contain rounded-lg"
                  />
                ) : (
                  <div className="flex flex-col items-center justify-center h-48">
                    <p className="text-white">Click to upload</p>
                  </div>
                )}
              </label>
            </div>
          </div>
          <div className="text-center space-y-4">
            <button
              type="button"
              onClick={handleTryOn}
              disabled={loading}
              className="bg-blue-600 text-white px-6 py-3 rounded-lg font-medium hover:bg-blue-700 disabled:opacity-50 disabled:cursor-not-allowed w-full md:w-auto"
            >
              {loading ? 'Processing...' : 'Generate Try-On Result'}
            </button>
            <button
              type="button"
              onClick={handleMatchingAnalysis}
              disabled={loading}
              className="bg-green-600 text-white px-6 py-3 rounded-lg font-medium hover:bg-green-700 disabled:opacity-50 disabled:cursor-not-allowed w-full md:w-auto"
            >
              {loading ? 'Analyzing...' : 'Analyze Fit'}
            </button>
          </div>
          <p className="text-sm mt-2 text-center text-white">
            You have used {tryOnCount} out of 3 try-ons this month.
          </p>
          {error && (
            <p className="text-red-500 text-center mt-4">{error}</p>
          )}
        </div>
      )}
      {resultImageUrl && (
        <div className="mt-8 w-full max-w-4xl bg-white rounded-lg shadow-md p-8 space-y-6">
          <h2 className="text-2xl font-medium text-gray-800 text-center">Step 2: Try-On Result</h2>
          <img
            src={resultImageUrl}
            alt="Try-On Result"
            className="rounded-lg shadow-md mx-auto max-h-96 object-contain"
          />
        </div>
      )}
    </div>
  );
};

export default VirtualTryOn;
